.blocks {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

  @media (min-width: 900px) {
    grid-template-columns: repeat(3, minmax(300px, 1fr));
  }

  &__block {
    display: grid;
    place-content: center;
    height: 300px;
    color: white;
    font-size: 100px;

    $colors:
      "1" rgb(100%, 0, 0),
      "2" rgb(80%, 0, 0),
      "3" rgb(60%, 0, 0),
      "4" rgb(40%, 0, 0),
      "5" rgb(20%, 0, 0),
      "6" rgb(0, 0, 0);

    @each $number, $color in $colors {
      &--#{$number} {
        background-color: $color;
      }
    }
  }
}
